import React from "react";
import Paper from "@material-ui/core/Paper";
import {
  CarouselProvider,
  Slider,
  Slide,
  ButtonBack,
  ButtonNext,
} from "pure-react-carousel";
import "pure-react-carousel/dist/react-carousel.es.css";
import { makeStyles } from "@material-ui/core/styles";
import Grid from "@material-ui/core/Grid";

function CharactorSelect() {
  const useStyle = makeStyles((theme) => ({
    image: {
      marginTop: 10,
      marginBottom: 10,
      width: 200,
      height: "auto",
    },
  }));

  const classes = useStyle();
  return (
    <Paper elevation={3}>
      <CarouselProvider
        naturalSlideWidth={100}
        naturalSlideHeight={60}
        totalSlides={3}
      >
        <Slider>
          <Slide index={0}>
            <Grid
              container
              spacing={3}
              direction="row"
              justify="center"
              alignItems="center"
            >
              <img
                className={classes.image}
                alt=""
                src={process.env.PUBLIC_URL + "/c_pink.png"}
              />
            </Grid>
          </Slide>
          <Slide index={1}>
            <Grid
              container
              spacing={3}
              direction="row"
              justify="center"
              alignItems="center"
            >
              <img
                className={classes.image}
                alt=""
                src={process.env.PUBLIC_URL + "/c_green.png"}
              />
            </Grid>
          </Slide>
          <Slide index={2}>
            <Grid
              container
              spacing={3}
              direction="row"
              justify="center"
              alignItems="center"
            >
              <img
                className={classes.image}
                alt=""
                src={process.env.PUBLIC_URL + "/c_violent.png"}
              />
            </Grid>
          </Slide>
        </Slider>
      </CarouselProvider>
    </Paper>
  );
}

export default CharactorSelect;
